<template>
  <div id="app">
    <el-container v-if="isLogin">
      <el-header height="60px">
        <div class="left">
          <img src="./assets/img/logo.png" alt="logo">
          <span>管理系统</span>
        </div>
        <div class="right">
          <ul>
            <li>
              <a href="javascript:;">
                <img src="./assets/img/exit.png" alt="用户">
                <span>用户：<em>张小三</em></span>
              </a>
            </li>
            <li>
              <a href="javascript:;">
                <img src="./assets/img/home.png" alt="首页">
                <span>首页</span>
              </a>
            </li>
            <li>
              <a href="javascript:;" @click="exit">
                <img src="./assets/img/exit.png" alt="退出">
                <span >退出</span>
              </a>
            </li>
          </ul>
        </div>
      </el-header>
      <el-container>
        <el-aside width="200px">
          <navBar></navBar>
        </el-aside>
        <el-container>
          <breadcrumb></breadcrumb>
          <el-main>
            <div class="main-content">
              <router-view></router-view>
            </div>
          </el-main>
          <el-footer height="30px">Footer</el-footer>
        </el-container>
      </el-container>
    </el-container>
    <router-view v-else></router-view>
  </div>
</template>

<script>
import navBar from './components/nav.vue'
import breadcrumb from './components/breadcrumb.vue'
export default {
  name: 'App',
  data(){
    return{
      isLogin:''
    }
  },
  components:{
    navBar,breadcrumb
  },
  methods:{
    exit(){
      this.$store.commit('updateLogin',false);
      this.$router.push({
        path: '/login'
      })
    }
  },
  updated(){
    this.isLogin = this.$store.state.login.isLogin;
  }
}
</script>

<style lang="scss" scoped="">
  #app{
    position: relative;
  }
.el-container,#app{
    height: 100%;
  }
.el-header{
  background: #1b7da2;
  padding-right: 0;
  padding-left: 5px;
  & .left{
    float: left;
    & img{
      height: 50px;
      vertical-align: middle;
      margin: 5px;
      box-sizing: border-box;
      padding: 5px;
      margin-right: 10px;
    }
    & span{
      vertical-align: middle;
      height: 60px;
      line-height: 60px;
      font-size: 18px;
      font-weight: bold;
      color: #fff;
    }
  }
  & .right{
    float: right;
    & ul{
      font-size: 0px;
      & li{
        display: inline-block;
        & a{
          display: block;
          color: #fff;
          font-size: 12px;
          font-weight: normal;
          opacity: 0.8;
          height: 60px;
          box-sizing: border-box;
          padding: 7px;
          width: 90px;
          text-align: center;
          border-left: 1px solid #1a6b8a;
          overflow: hidden;
          &:hover{
            background: #207d96;
          }
          & img{
            width: 26px;
            height: 26px;
            display: block;
            margin: 0 auto 3px;
          }
        }
      }
    }
  }
}
.el-aside{
  background: #2a323a;
}
.el-footer{
  height: 30px;
  background: #20252b;
  color: #ddd;
  line-height: 30px;
  text-align: center;
  font-size: 12px;
}
.el-main{
  padding: 0;
}
.main-content{
  padding: 20px;
}
</style>



