<template>
  <div class="wrap">
    <div class="logo">
      <img src="../assets/img/logo.png" alt="">
    </div>
    <el-card class="login-wrap">
      <h3>系统登陆</h3>
      <el-form :model="loginForm" status-icon ref="loginForm" label-width="0" class="demo-ruleForm">
        <el-form-item>
          <el-input v-model="loginForm.username" placeholder="用户名"></el-input>
        </el-form-item>
        <el-form-item>
          <el-input v-model="loginForm.password" placeholder="密码"></el-input>
        </el-form-item>
        <el-form-item class="isRemember">
          <el-checkbox-group v-model="loginForm.isRemember">
            <el-checkbox label="记住密码" name="type"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item class="btnsLine">
          <el-button type="primary" @click="login">登陆</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
    import ElCard from "../../node_modules/element-ui/packages/card/src/main.vue";

    export default {
      components: {ElCard},
      name: '',
      data(){
        return{
          loginForm:{
            username:'',
            password:'',
            isRemember:''
          }
        }
      },
      methods:{
        login(){
          this.$ajax.get('http://rap2api.taobao.org/app/mock/data/17100',{
              params:{
                username:'456'
              }
            })
            .then((response)=>{
              var data = response.data;
              if(data.isLogin){
                this.$store.commit('updateLogin',data);
                let redirect = decodeURIComponent(this.$route.query.redirect || '/');
                this.$router.push({
                  path: redirect
                })
              }

            })
            .catch(function (error) {

            })
        }
      }
    }
</script>
<style scoped lang="scss">
.wrap{
  background:#2a323a!important;
  height: 100%;
  width: 100%;
}
.logo img{
  width: 200px;
  margin: 20px;
}
.login-wrap{
  width: 500px;
  height: 300px;
  margin: 0 auto;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  & h3{
    font-weight: bold;
    text-align: center;
    margin-bottom: 30px;
  }
  & .el-form-item{
    margin-bottom: 15px;
  }
  & .isRemember{
    margin-bottom: 5px;
  }
  & .isRemember /deep/ .el-form-item__content{
    line-height: 20px;
  }
  & .btnsLine button{
    width: 100%;
    background: #1b7da2;
    border-color: #1b7da2;
  }
  & /deep/ .el-checkbox__input.is-checked+.el-checkbox__label{
    color: #1b7da2;
  }
  & /deep/ .el-checkbox__input.is-checked .el-checkbox__inner, .el-checkbox__input.is-indeterminate .el-checkbox__inner{
    background: #1b7da2;
    border-color: #1b7da2;
  }
  & /deep/ .el-checkbox__inner:hover{
    border-color: #1b7da2;
  }
}
</style>



