<template>
  <el-menu
    background-color="#2a343e"
    text-color="#fff"
    active-text-color="#fff"
    :default-active=curNav
    router>
    <div v-for="item in nav">
      <!--没有有下拉菜单-->
      <el-menu-item
        v-if="!item.children && item.meta.isNav"
        :index=item.path>
        <!--<i :src="item.meta.icon"></i>-->
        <span slot="title">{{item.name}}</span>
      </el-menu-item>
      <!--有下拉菜单-->
      <el-submenu
        v-if="item.children && item.meta.isNav"
        :index= item.path>
        <template slot="title">
          <!--<i :src="item.meta.icon"></i>-->
          <span>{{item.name}}</span>
        </template>
        <div v-for="child in item.children">
          <el-menu-item
            :index=child.path>{{child.name}}</el-menu-item>
        </div>
      </el-submenu>
    </div>
  </el-menu>
</template>
<script>
    export default {
      name: 'navBar',
      data(){
        return {
          nav:[],
          curNav:''
        }
      },
      methods:{
      },
      mounted(){
        this.nav = this.$router.options.routes;
        this.curNav = this.$store.state.curNav;
      }
    }
</script>
<style scoped lang="scss">
  .el-menu-item,.el-submenu{
    width: 200px;
  }
  aside>ul.el-menu>li{
    border-bottom: 1px solid #25292d;
  }
  .el-menu--inline>li{
    border-top: 1px solid #25292d;
  }
  /deep/ .el-menu-item, /deep/ .el-submenu__title{
    height: 50px;
    line-height: 50px;
  }
  .el-menu-item.is-active{
    position: relative;
    background: #222a32!important;
    &:before{
      position: absolute;
      left: 0px;
      top: 0px;
      content: " ";
      width: 0;
      /* background: #ccc; */
      border-left: 4px solid #05a2ba;
      height: 50px;
     }
  }
  /deep/ .el-icon-arrow-down{
    font-size: 16px;
    color: #fff;
    &:before{
      content: "\E60B";
    }
  }


</style>



