export default {
  //更新导航栏
  updateNav(state,newNav){
    state.curNav = newNav;
  },
  //更新登陆状态
  updateLogin(state,data){
    if(data == false){
      state.login.isLogin = false;
      state.login.userName = '';
    }else{
      state.login.isLogin = true;
      state.login.userName = data.name;
    }
  }
}



