/*
 * list 列表
 * edit 编辑
 * detail 详情
 */
import groupTwo from '@/view/groupTwo/groupTwo.vue'
import list from '@/view/groupTwo/list.vue'

export default [
  {
    path:'/groupTwo',
    name:'分类二',
    component:groupTwo,
    redirect:'/groupTwo/list',
    meta:{
      isNav:true
    },
    children:[
      {
        path:'/groupTwo/list',
        name:' 组二列表',
        component:list,
        meta:{
          isNav:true
        },
      }
    ]
  }
]



