<template>
  <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item>
      <i class="el-icon-location-outline"></i>
      首页</el-breadcrumb-item>
    <el-breadcrumb-item
      v-for="(item,index) in brumblist"
      :key="index"
    >{{item.name}}</el-breadcrumb-item>
  </el-breadcrumb>
</template>

<script>
    export default {
      name: 'breadcrumb',
      data(){
         return{
           brumblist:''
         }
      },
      created(){
        this.getBreadcrumb();
      },
      watch:{
        $route(to,from){
          if(to.matched[0].name != '首页'){
            this.brumblist = to.matched;
            this.$store.commit('updateNav',this.brumblist[this.brumblist.length-1].path)
          }else{
            this.brumblist=''
          }
        }
      },
      methods:{
        getBreadcrumb(){
          if(this.$route.matched[0].name != '首页'){
            this.brumblist = this.$route.matched;
          }else{
            this.brumblist=''
          }
        },
      }
    }
</script>
<style scoped lang="scss">
.el-breadcrumb{
  background: #fff;
  height: 30px;
  box-sizing: border-box;
  line-height: 30px;
  border-bottom: 1px solid #ddd;
  font-size: 14px;
  padding-left: 10px;
  & /deep/ .el-breadcrumb__inner{
    font-weight: normal;
    font-size: 12px;
    color: #999;
    &:hover{
      color: #999;
      cursor: default;
    }
  }
}
</style>



