/*
 * list 列表
 * edit 编辑
 * detail 详情
 */
import groupOne from '@/view/groupOne/groupOne.vue'
import list from '@/view/groupOne/list.vue'
import edit from '@/view/groupOne/edit.vue'
import detail from '@/view/groupOne/detail.vue'

export default [
  {
    path:'/groupOne',
    name:'分类一',
    component:groupOne,
    redirect:'/groupOne/list',
    meta:{
      isNav:true
    },
    children:[
      {
        path:'/groupOne/list',
        name:'列表',
        component:list,
        meta:{
          isNav:true
        },
      },{
        path:'/groupOne/edit',
        name:'编辑',
        component:edit,
        meta:{
          isNav:true
        },
      },{
        path:'/groupOne/detail',
        name:'详情',
        component:detail,
        meta:{
          isNav:true
        },
      }
    ]
  }
]



